body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

@keyframes infinite-spin {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes {
  transform-origin: bottom;
  z-index: 1;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite infinite-spin;
  position: absolute;
  bottom: 50%;
}

.stopwatch__seconds {
  transform-origin: bottom;
  z-index: 2;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite infinite-spin;
  position: absolute;
  bottom: 50%;
}

.stopwatch__center {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
}

.stopwatch--speed-up .stopwatch__minutes {
  animation-duration: 600s;
}

.stopwatch--speed-up .stopwatch__seconds {
  animation-duration: 10s;
}
